@use 'Base/variables' as v;
@use 'Base/mixins' as m;

.containerIndex{
    display: block;
    width: 80%;
    align-items: center;
    margin: auto;
     @include m.tablet{
        width: 100%;
        display: flex;
        justify-content: space-between;
     }
    
        .indexizq{
            margin-bottom: 450px;
            h1{
                text-align: center;
                font-size: 5rem;
            }
            h2{
                color:v.$primario;
            }
            @include m.tablet{
                margin-bottom: 0%;
                width: 50%;
            }
            
        }
        .indexDer{
            margin: auto;
            @include m.tablet{
                width: 40%;
            }
            
            .recuperarConEnlace
            {
                margin-top: 0%;
                align-items: center;
                text-align: center;
                a{  
                    cursor: pointer;
                    font-size: 1.2rem;
                    color: v.$negro;
                    @include m.tablet{
                        font-size: 2rem;
                    }
                }
            }
            .primerahex{
            position: absolute;
            transform: translate(-40px,-400px);
                @include m.tablet{
                    position: relative;
                    transform: translateY(25px);
                }
                @include m.desktop{
                position: absolute;
                transform: translate(-30px, -205px);
                }
                
            }
            .segunhex{
                position:absolute;
                transform: translate(120px,-310px);
                 @include m.tablet{
                    position: relative;
                    margin-bottom:0px ;
                    transform: translate(0);
                 }
                    @include m.desktop{
                        position: absolute;
                        transform: translate(210px, -75px);
                        margin-bottom:0px ;
                    }
               
            }

            .hexagon{
                margin: auto;
                position: relative;
                width: 200px;
                height: 200px;
                @include m.tablet{
                    width: 250px;
                    height: 250px;
                }
                @include m.desktop{
                    width: 300px;
                    height: 300px;
                }
               
            }
            .hexagon img{
                width: 100%;
                height: 100%;
            }
            .textEnlaceIndex{
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
            }
            .textEnlaceIndex a{
                font-weight: bold;
                color: v.$primario;
                font-size: 1.5rem;
                @include m.tablet{
                    font-size: 2rem;
                }
               
            }
            
        }
}